                                                                                         value=1></v-radio>
                                                                                <v-radio label="No"
                                                                                         value=0></v-radio>

                                                                            </v-radio-group>
                                                                        </template>
                                                                        <template
                                                                                v-if="theoretical_index.includes(index+71)">
