                                                            {{reflexive_total}} puntos</p>
                                                        <p class="subtitle-1 mb-0">Teórico:</p>
                                                        <p class="text-center mb-0 font-weight-medium">
                                                            {{theoretical_total}} puntos</p>
                                                        <p class="subtitle-1 mb-0 ">Pragmatico:</p>
                                                        <p class="text-center mb-0 font-weight-medium">
                                                            {{pragmatic_total}} puntos</p>
                                                    </div>
                                                </v-card-text>
                                            </v-card>
                                        </v-col>
