                                        </v-col>

                                    </v-row>

                                    <v-row v-show="next!=false" no-gutters>
                                        <v-spacer></v-spacer>
                                        <v-col cols="12">
                                            <v-card
                                                    v-show="max_total == active_total"
                                                    class="mb-12 elevation-0"
                                                    color="grey lighten-5"
                                            >
