                                                    <p class="text-justify">
                                                        {{style_text.active.description}}
                                                    </p>
                                                </v-card-text>
                                                <v-card-actions>
                                                    <v-spacer></v-spacer>

                                                    <v-btn text color="primary" :href="style_text.active.url">
                                                        Ver mas
                                                        <v-icon class="material-icons">live_help</v-icon>
                                                    </v-btn>

                                                </v-card-actions>
                                            </v-card>
                                            <v-card

                                                    v-show="max_total == reflexive_total"
                                                    class="mb-12 elevation-0"
                                                    color="grey lighten-5"
                                            >
                                                <v-card-title>
                                                    {{style_text.reflexive.name}}
                                                </v-card-title>
                                                <v-card-text>
                                                    <p class="text-justify">
                                                        {{style_text.reflexive.description}}
                                                    </p>
                                                </v-card-text>
                                                <v-card-actions>
                                                    <v-spacer></v-spacer>

                                                    <v-btn text color="primary" :href="style_text.reflexive.url">
                                                        Ver mas
                                                        <v-icon class="material-icons">live_help</v-icon>
                                                    </v-btn>

                                                </v-card-actions>
                                            </v-card>
                                            <v-card

                                                    v-show="max_total == theoretical_total"
                                                    class="mb-12 elevation-0"
                                                    color="grey lighten-5"
                                            >
                                                <v-card-title>
                                                    {{style_text.theoretical.name}}
                                                </v-card-title>
                                                <v-card-text>
                                                    <p class="text-justify">
                                                        {{style_text.theoretical.description}}
                                                    </p>
                                                </v-card-text>
                                                <v-card-actions>
                                                    <v-spacer></v-spacer>

                                                    <v-btn text color="primary" :href="style_text.theoretical.url">
                                                        Ver mas
                                                        <v-icon class="material-icons">live_help</v-icon>
                                                    </v-btn>

                                                </v-card-actions>
                                            </v-card>
                                            <v-card

                                                    v-show="max_total == pragmatic_total"
                                                    class="mb-12 elevation-0"
                                                    color="grey lighten-5"

                                            >
                                                <v-card-title>
                                                    {{style_text.pragmatic.name}}
                                                </v-card-title>
                                                <v-card-text>
                                                    <p class="text-justify">
                                                        {{style_text.pragmatic.description}}
                                                    </p>
                                                </v-card-text>
                                                <v-card-actions>
                                                    <v-spacer></v-spacer>

                                                    <v-btn text color="primary" :href="style_text.pragmatic.url">
                                                        Ver mas
                                                        <v-icon class="material-icons">live_help</v-icon>
                                                    </v-btn>

                                                </v-card-actions>
                                            </v-card>
                                        </v-col>
                                    </v-row>


                                </v-container>

                                <v-card-actions>

                                    <v-spacer></v-spacer>
                                    <v-container>
                                        <v-row no-gutters>
                                            <v-col
                                                    cols="12"
                                                    xs="12"
                                                    sm="6"
                                                    md="6"
                                                    lg="6"
                                                    xlg="6"
                                            >
                                                <v-btn v-show="next!=true" color="primary" @click.prevent="next=true">
                                                    Continuar
                                                    <v-icon class="material-icons">skip_next</v-icon>
                                                </v-btn>
                                                <v-btn v-show="next==true" color="primary" @click.prevent="next=false">
                                                    Regresar
                                                    <v-icon class="material-icons">skip_previous</v-icon>
                                                </v-btn>
                                            </v-col>
                                            <v-col
                                                    cols="12"
                                                    xs="12"
                                                    sm="6"
                                                    md="6"
                                                    lg="6"
                                                    xlg="6"
                                            >
                                                <v-btn color="warning" @click.prevent="reset_test">
                                                    <v-icon class="material-icons">refresh</v-icon>
