                            </template>

                        </v-card>
                    </v-content>
                </v-col>
            </v-row>

        </v-container>
    </v-app>
</template>

<script>
    export default {
        name: "CuestionarioEstilos",
        /*Variables a retornar*/
        props: {
            ligaA: String,
            ligaR: String,
            ligaT: String,
            ligaP: String,
        },
        data() {
            return {
                step_count: 1,
                started_test: false,
                evaluation_test: false,
                instructions_text: [
                    'Este cuestionario ha sido diseñado para identificar tu estilo de aprendizaje, NO es un test de inteligencia, NI de personalidad.',
                    'No hay limite de tiempo para contestar el cuestionario.',
                    'No hay respuestas correctas ni erroneas.',
                    'Procura ser lo mas sincero posible con tus respuestas.'
                ],
                /*Array de preguntas*/
                questions_data: [
                    'Tengo fama de decir lo que pienso claramente y sin rodeos.',
                    'Estoy seguro(a) de lo que es bueno y lo que es malo, lo que está bien y lo que está mal.',
                    'Muchas veces actúo sin mirar las consecuencias.',
                    'Normalmente trato de resolver los problemas metódicamente y paso a paso.',
                    'Creo que los formulismos coartan y limitan la actuación libre de las personas.',
                    'Me interesa saber cuáles son los sistemas de valores de los demás y con qué criterios actúan.',
                    'Pienso que el actuar intuitivamente puede ser siempre tan válido como actuar reflexivamente',
                    'Creo que lo más importante es que las cosas funcionen.',
                    'Procuro estar al tanto de lo que ocurre aquí y ahora.',
                    'Disfruto cuando tengo tiempo para preparar mi trabajo y realizarlo a conciencia.',
                    'Estoy a gusto siguiendo un orden, en las comidas, en el estudio, haciendo ejercicio regularmente.',
                    'Cuando escucho una nueva idea, enseguida comienzo a pensar cómo ponerla en práctica.',
                    'Prefiero las ideas originales y novedosas aunque no sean prácticas.',
                    'Admito y me ajusto a las normas sólo si me sirven para lograr mis objetivos.',
                    'Normalmente encajo bien con personas reflexivas, y me cuesta sintonizar con personas demasiado espontáneas, imprevisibles.',
                    'Escucho con más frecuencia de lo que hablo.',
                    'Prefiero las cosas estructuradas a las desordenadas.',
                    'Cuando poseo cualquier información, trato de interpretarla bien antes de manifestar alguna conclusión.',
                    'Antes de hacer algo estudio con cuidado sus ventajas e inconvenientes.',
                    'Crezco con el reto de hacer algo nuevo y diferente.',
                    'Casi siempre procuro ser coherente con mis criterios y sistemas de valores. Tengo principios y los sigo.',
                    'Cuando hay una discusión no me gusta ir con rodeos.',
                    'Me disgusta implicarme afectivamente en mi ambiente de trabajo. Prefiero mantener relaciones distantes.',
                    'Me gustan más las personas realistas y concretas que las teóricas.',
                    'Me cuesta ser creativo(a), romper estructuras.',
                    'Me siento a gusto con personas espontáneas y divertidas.',
                    'La mayoría de las veces expreso abiertamente cómo me siento.',
                    'Me gusta analizar y dar vueltas a las cosas.',
                    'Me molesta que la gente no se tome en serio las cosas.',
                    'Me atrae experimentar y practicar las últimas técnicas y novedades.',
                    'Soy cauteloso(a) a la hora de sacar conclusiones.',
                    'Prefiero contar con el mayo número de fuentes de información. Cuantos más datos se reúnan para reflexionar, mejor.',
                    'Tiendo a ser perfeccionista.',
                    'Prefiero oír las opiniones de los demás antes de exponer la mía.',
                    'Me gusta afrontar la vida espontáneamente y no tener que planificar todo previamente.',
                    'En las discusiones me gusta observar cómo actúan los demás participantes.',
                    'Me siento incómodo(a) con las personas calladas y demasiado analíticas.',
                    'Juzgo con frecuencia las ideas de los demás por su valor práctico.',
                    'Me agobio si me obligan a acelerar mucho el trabajo para cumplir un plazo.',
                    'En las reuniones, apoyo las ideas prácticas y realistas.',
                    'Es mejor gozar del momento presente que deleitarse pensando en el pasado o en el futuro.',
                    'Me molestan las personas que siempre desean apresurar las cosas.',
                    'Aporto ideas nuevas y espontáneas en los grupos de discusión.',
                    'Pienso que son más consistentes las decisiones fundamentadas en un minucioso análisis que las basadas en la intuición.',
                    'Detecto frecuentemente la inconsistencia y puntos débiles en las argumentaciones de los demás.',
                    'Creo que es preciso saltarse las normas muchas más veces que cumplirlas.',
                    'A menudo caigo en la cuenta de otras formas mejores y más prácticas de hacer las cosas.',
                    'En conjunto hablo más de lo que escucho.',
                    'Prefiero distanciarme de los hechos y observarlos desde otras perspectivas.',
                    'Estoy convencido(a) que debe imponerse la lógica y el razonamiento.',
                    'Me gusta buscar nuevas experiencias.',
                    'Me gusta experimentar y aplicar las cosas.',
                    'Pienso que debemos llegar pronto al grano, al meollo de los temas.',
                    'Siempre trato de conseguir conclusiones e ideas claras',
                    'Prefiero discutir cuestiones concretas y no perder el tiempo con charlas vacías.',
                    'Me impaciento cuando me dan explicaciones irrelevantes e incoherentes.',
                    'Compruebo antes si las cosas funcionan realmente',
                    'Hago varios borradores antes de la redacción definitiva de un trabajo.',
                    'Soy consciente de que en las discusiones ayudo a mantener a los demás centrados en el tema, evitando divagaciones.',
                    'Observo que, con frecuencia, soy uno(a) de los(as) más objetivos(as) y desapasionados(as) en las discusiones.',
                    'Cuando algo va mal, le quito importancia y trato de hacerlo mejor.',
                    'Rechazo ideas originales y espontáneas si no las veo prácticas.',
                    'Me gusta sopesar diversas alternativas antes de tomar una decisión.',
                    'Con frecuencia miro hada delante para prever el futuro.',
                    'En los debates y discusiones prefiero desempeñar un papel secundario antes que ser el(la) líder o el(la) que más participa.',
                    'Me molestan las personas que no actúan con lógica.',
                    'Me resulta incómodo tener que planificar y prever las cosas.',
                    'Creo que el fin justifica los medios en muchos casos.',
                    'Suelo reflexionar sobre los asuntos y problemas.',
                    'El trabajar a conciencia me llena de satisfacción y orgullo.',
                    'Ante los acontecimientos trato de descubrir los principio y teorías en que se basan.',
                    'Con tal de conseguir el objetivo que pretendo, soy capaz de herir sentimientos ajenos.',
                    'No me importa hacer todo lo necesario para que sea efectivo mi trabajo.',
                    'Con frecuencia soy una de las personas que más anima las fiestas.',
                    'Me aburro enseguida en el trabajo metódico y minucioso.',
                    'La gente con frecuencia cree que soy poco sensible a sus sentimientos.',
                    'Suelo dejarme llevar por mis intuiciones.',
                    'Si trabajo en grupo procuro que se siga un método y un orden.',
                    'Con frecuencia me interesa averiguar lo que piensa la gente.',
                    'Esquivo los temas subjetivos, ambiguos y poco claros.',


                ],
                /*Array de tipos*/
                active_index: [3, 5, 7, 9, 13, 20, 26, 27, 35, 37, 41, 43, 46, 48, 51, 61, 67, 74, 75, 77],
                reflexive_index: [10, 16, 18, 19, 28, 31, 32, 34, 36, 39, 42, 44, 49, 55, 58, 63, 65, 69, 70, 79],
                theoretical_index: [2, 4, 6, 11, 15, 17, 21, 23, 25, 29, 33, 45, 50, 54, 60, 64, 66, 71, 78, 80],
                pragmatic_index: [1, 8, 12, 14, 22, 24, 30, 38, 40, 47, 52, 53, 56, 57, 59, 62, 68, 72, 73, 76],
                /*Array de respuestas*/
                question_results: Array(80).fill(NaN),
                /*Valores por estilo*/
                active_total: 0,
                reflexive_total: 0,
                theoretical_total: 0,
                pragmatic_total: 0,
                max_total: 0,
                /*Descripciones por estilo y aqui mismo añadir la url de ayuda*/
                next: false,
                style_text: {
                    active: {
                        name: 'Estilo de aprendizaje activo.',
                        description:
                        'Los alumnos activos se involucran totalmente y sin prejuicios en las experiencias nuevas, ' +
                        'suelen disfrutar el momento presente y se dejan llevar por los acontecimientos, ' +
                        'por lo general suelen ser entusiastas ante lo nuevo y tienden a actuar primero y ' +
                        'pensar después en las consecuencias.\n' +
                        'Llenan sus días de actividades y tan pronto disminuye el encanto de una de ellas se lanzan ' +
                        'a la siguiente, asi como tambien les aburre ocuparse de planes a largo plazo y consolidar los proyectos, les gusta trabajar ' +
                        'rodeados de gente, pero siendo el centro de las actividades.',
                        url:this.ligaA

                    },
                    reflexive: {
                        name: 'Estilo de aprendizaje reflexivo.',
                        description: "Los alumnos reflexivos tienden a adoptar la postura de un observador que analiza sus\n" +
                        "experiencias desde muchas perspectivas distintas, suelen recoger datos y los analizan detalladamente antes de llegar a una conclusión. \n" +
                        "Para ellos lo más importante es la recolección de datos y su análisis concienzudo, así que procuran\n" +
                        "posponer las conclusiones todo lo que pueden ademas de ser precavidos y analizar todas las implicaciones de cualquier acción antes de ponerse en\n" +
                        "movimiento. En las reuniones observan y escuchan antes de hablar, procurando pasar desapercibidos.",
                        url: this.ligaR
                    },
                    theoretical: {
                        name: 'Estilo de aprendizaje teórico.',
                        description: 'Los alumnos teóricos adaptan e integran las observaciones que realizan en teorías\n' +
                        'complejas y bien fundamentadas lógicamente, piensan de forma secuencial y paso a paso, integrando hechos dispares en teoría\n' +
                        'coherentes.\n' +
                        'Les gusta analizar y sintetizar la información y su sistema de valores premia la lógica y la\n' +
                        'racionalidad asi como tambien llegan a sentirse incómodos con los juicios subjetivos, las técnicas de pensamiento lateral y las\n' +
                        'actividades fuera de lógica clara.',
                        url:this.ligaT
                    },
                    pragmatic: {
                        name: 'Estilo de aprendizaje pragmatico.',
                        description: "A los alumnos pragmáticos les gusta probar ideas, teorías y técnicas nuevas y comprobar si\n" +
                        "funcionan en la práctica, inmediatamente les aburren e impacientan las largas discusiones, sobre la misma idea de\n" +
                        "forma interminable. Son básicamente gente práctica, apegada a la realidad, a la que le gusta tomar decisiones\n" +
                        "y resolver problemas, para ellos los problemas son un desafío y siempre están buscando una manera mejor de hacer las\n" +
                        "cosas. ",
                        url:this.ligaP
                    },
                },
                /*Series para la grafica*/
                series_spider: [{
                    name: 'Puntuación',
                    data: [0, 0, 0, 0],
                }],
                series_pie: [0, 0, 0, 0],
                /*Opciones de la grafica*/
                chartOptions: {
                    yaxis: {
                        min: 0,
                        tickAmount: 4,
                        max: 20
                    },
                    labels: ['Activo', 'Reflexivo', 'Teórico', 'Pragmatico'],
                    plotOptions: {
                        radar: {
                            polygons: {
                                strokeColor: '#e9e9e9',
                                fill: {
                                    colors: ['#f8f8f8', '#fff']
                                }
                            }
                        }
                    },
                },
                chartOptions_pie: {
                    labels: ['Activo', 'Reflexivo', 'Teórico', 'Pragmatico'],
                    responsive: [{
                        breakpoint: 400,
                        options:{
                            chart:{
                                width:170
                            }
                        }
                    }], legend: {
                        position: 'bottom'
                    }, chart: {
                        width: 400,
                        toolbar: {
                            show: true,
                            tools: {
                                download: true,
                            },
                            autoSelected: 'zoom'
                        },
                    },
                },
                /*Opciones alert*/
                snackbar: false,
                timeout: 12000,

            }
        },
        /*Declaración de metodos*/
        methods: {
            start_test() {
                this.started_test = true;
            },
            make_evaluation(min_index, max_index) {
                if (this.question_results.slice(min_index, max_index).includes(NaN)) {
                    this.snackbar = true
                } else {
                    this.active_total = 0;
                    this.reflexive_total = 0;
                    this.theoretical_total = 0;
                    this.pragmatic_total = 0;

                    this.active_index.forEach(index => {
                        this.active_total += parseInt(this.question_results[index - 1]);
                    });
                    this.reflexive_index.forEach(index => {
                        this.reflexive_total += parseInt(this.question_results[index - 1]);
                    });
                    this.theoretical_index.forEach(index => {
                        this.theoretical_total += parseInt(this.question_results[index - 1]);
                    });
                    this.pragmatic_index.forEach(index => {
                        this.pragmatic_total += parseInt(this.question_results[index - 1]);
                    });
                    this.evaluation_test = true;
                    this.series_spider = [{
                        name: 'Puntuación',
                        data: [this.active_total, this.reflexive_total, this.theoretical_total, this.pragmatic_total]
                    }];
                    this.series_pie = [this.active_total, this.reflexive_total, this.theoretical_total, this.pragmatic_total];
                    this.max_total = Math.max(this.active_total, this.reflexive_total, this.theoretical_total, this.pragmatic_total);
                }
            },
            reset_test() {
                this.started_test = false;
                this.evaluation_test = false;
                this.step_count = 1
                this.question_results = Array(80).fill(NaN)
            },
            validate_test(min_index, max_index, step) {
                if (this.question_results.slice(min_index, max_index).includes(NaN)) {
                    this.snackbar = true
                } else {
                    this.step_count = step;
                }
            }
        },

    }
</script>

<style scoped>
    @import "~material-design-icons-iconfont/dist/material-design-icons.css";
</style>